<template>
    <div class="head">
        <ul class="headNav">
            <li>
                <p>账号: <span class="name">{{user}}</span></p>
            </li>
            <li>
                <h2>首页</h2>
            </li>
            <li>
                <mt-button type="primary" @click="signout">退出</mt-button>
            </li>
        </ul>
    </div>
</template>
<script>
import { MessageBox } from 'mint-ui';
export default {
    name:"Head",
    data () {
        return {
            user:'',
            title:''
        }
    },
    created () {
        let name = sessionStorage.getItem('useName')
        this.user = name
    },
    methods:{
        signout(){
            MessageBox.confirm('确定要退出吗?').then(action => {
                    sessionStorage.removeItem('useName')
                    this.$router.push({name:'Login'})
            }).catch(err=>{
                
            })

        }
    }
}
</script>
<style scoped>
.head{
    width: 100%;
    height: 50px;
    position: fixed;
    top: 0;
    background: #1288e052;
    line-height: 30px;
    text-align: center;
    line-height: 50px;
}
.headNav{
    height: 100%;
   display: flex;
}
.headNav li{
    flex: 1;
}
.headNav .mint-button{
    font-size: 13px;
    border-radius: 5px;
    height: 30px;
}
.headNav .name{
    font-weight: bold;
}
</style>