<template>
  <div class="footer">
    <ul class="headNav">
      <li v-for="(item, index) in navList" :key="index" @click="navBtn(item,index)" :class="[currentIndex == index?'current':'']">
        <span :class="['iconfont', item.icon]"></span>
        <span>{{ item.title }}</span>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: "Footer",
  data() {
    return {
      navList: [
        { title: "首页", icon: "icon-shouye", path:'HomeConten'},
        { title: "天气", icon: "icon-tianqi", path:'Weather' },
        { title: "图书", icon: "icon-tushuguan_library", path:'Book' },
        { title: "我的", icon: "icon-wode", path:'Mine' },
      ],
      currentIndex:0
    };
  },
  methods:{
      navBtn(obj,index){
          this.currentIndex = index
          this.$router.push({name:obj.path})
      }
  }
};
</script>
<style scoped>
.footer {
  width: 100%;
  height: 50px;
  position: fixed;
  bottom: 0;
  background: #005da06b;
  text-align: center;
}
.headNav {
  height: 100%;
  display: flex;
}
.headNav li {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
 .current {
  color: #000;
  font-weight: bolder;
  text-shadow: 1px 1px 1px #888;
}
</style>